<template>
  <div>
    <van-sticky>
      <van-field v-model="text" center clearable label="输入tag" placeholder="请输入tag">
        <template #button>
          <van-button size="small" @click="onRefresh" type="primary">搜索</van-button>
        </template>
      </van-field>
      <!--  <div style="">
        <div>
          <van-switch v-model="checked" />
        </div>
        <div>显示标签</div>
      </div> -->
    </van-sticky>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="到底了~" @load="onLoad">
        <div id="imgArea">
          <div v-for="(item,index) in imglist" :key="item.id" class="imgArea">
            <img :src="item.preview_url" @click="OpenImg(item,index);" />
            <!-- {{item.preview_url}} -->
            <div>作者:{{item.author}}</div>
            <!-- <div v-show="showtag">tags:{{item.tags}}</div> -->
            <div class="infoArea">
              <div>分辨率：{{item.jpeg_height}}*{{item.jpeg_width}}</div>
              <div>原图大小：{{(item.file_size/1024).toFixed(2)}}kb</div>
            </div>
            <div class="downBtn" @click="DownImg(item)">
              下载
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
    <van-image-preview @change="OpenChange" @scale="OpenScale" :start-position="openIndex" v-model="openImagesModel"
      :images="openImages">
      <template v-slot:cover style="width: 100%;">
        <div class="openAreaText">原图分辨率/大小:{{imgInfo.jpeg_height}}*{{imgInfo.jpeg_width}}/{{(imgInfo.file_size/1024).toFixed(2)}}kb</div>
        <div class="opendownBtn" @click="DownImg(imgInfo)">下载</div>
      </template>
    </van-image-preview>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        imglist: [],
        text: '',
        refreshing: false, //是否正在下拉刷新
        loading: false, //是否正在加载
        finished: false, //是否已加载完成
        PageIndex: 1, //分页参数
        PageSize: 10,
        openImages: [], //大图预览
        openImagesModel: false,
        openIndex: 0, //当前第几张
        imgInfo: {}, //当前选中的图片信息
        showtag: true

      }
    },
    mounted: function() {
      this.GetData();
    },
    methods: {
      DownImg(item) {
        if (!window.plus) {
          this.$toast(`不支持下载到相册，请使用其他方式下载`);
        } else {
          plus.gallery.save(item.file_url, data => {
            this.$toast(`已经保存到` + data.path);
          }, error => {
            this.$toast(error.message);
          });
        };
      },
      OpenChange(index) {
        this.imgInfo = this.imglist[index];
        if (this.imglist.length - index == 4) {
          this.onLoad();
          this.$toast(`加载开始第${this.PageIndex}页`);
        }
      },
      OpenScale(obj) {
        if (obj.scale > 1) {
          this.openImages[obj.index] = this.imglist[obj.index].file_url;
        }
      },
      OpenImg(item, index) {
        this.$toast(`放大图片加载原图`);
        this.openIndex = index;
        this.imgInfo = item;
        this.openImagesModel = true;
      },
      GetData() {
        this.common.ajax.AjaxHelper.AllList(this,
          `limit=${this.PageSize}&page=${this.PageIndex}&tags=${this.text}`, data => {
            if (data.status == 200) {
              this.imglist = this.imglist.concat(data.data);
              this.openImages = this.imglist.map(x => x.jpeg_url);
              this.loading = false;
              this.refreshing = false;
            } else {
              this.$toast(data.Message)
              this.finished = true;
              this.refreshing = false;
            }
          }, data => {
            this.$toast(data.Message)
            this.finished = true;
            this.refreshing = false;
          });
      },
      //下拉刷新
      onRefresh() {
        this.PageIndex = 1;
        this.imglist = [];
        this.loading = false;
        this.finished = false;
        this.GetData();
      },
      //上拉加载
      onLoad() {
        this.PageIndex++;
        this.GetData();
      },
    }
  }
</script>

<style scoped>
  #imgArea {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }

  #imgArea>div {
    width: 300px;
    margin: 10px;
    position: relative;
  }

  #imgArea .downBtn {
    width: 45px;
    height: 30px;
    position: absolute;
    bottom: 35px;
    right: -10px;
    background: #23ade5;
    text-align: center;
    line-height: 33px;
    color: #fff;
    border-radius: 5px;
    font-size: 18px;
  }

  .infoArea {
    display: flex;
    justify-content: space-between;
  }

  .openAreaText {
    color: #fff;
    width: 100%;
    font-size: 14px;
    text-align: center;
    margin-top: 30px;
    text-shadow: 0 1px 1px #323233;
    font-size: 14px;
  }

  .opendownBtn {
    width: 50px;
    position: fixed;
    bottom: 15px;
    right: 15px;
    text-align: center;
    color: #fff;
    border: 1px solid #fff;
  }

  >>>.van-image-preview__cover {
    width: 100%;
  }
</style>



