var AjaxHelper = {
  allList: "post.json",
  //GET
  ajaxGet: function(_this, url, jsonParam, success, error) {
    _this.$axios.get('/api' + url + "?" + jsonParam).then(data => {
        success(data);
      })
      .catch(data => {
        error(data);
      })
  },
  //Post
  ajaxPost: function(_this, url, jsonParam, success, error) {
    let PostFunction = null;
    _this.$axios.post(this.urlHearder + url, jsonParam)
      .then(data => {
        success(data);
      })
      .catch(data => {
        error(data);
      })
  },
  //Post
  ajaxPost: function(_this, url, jsonParam, success, error) {
    let PostFunction = null;
    _this.$axios.post(this.urlHearder + url, jsonParam)
      .then(data => {
        success(data);
      })
      .catch(data => {
        error(data);
      })
  },
  AllList: function(_this, jsonParam, success, error) {
    this.ajaxGet(_this, this.allList, jsonParam, success, error);
  },
};

export default {
  AjaxHelper
}



