// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'vant/lib/index.css';

import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios';
import ApiUrl from './assets/JS/ApiUrl.js';
import a from '../vue.config.js';


Vue.prototype.common = {};
Vue.prototype.common.ajax = ApiUrl;
axios.defaults.headers.post['Content-Type']= "application/json;charset=UTF-8";
Vue.prototype.$axios = axios;

Vue.config.productionTip = false;

import {
  Button,
  Col,
  Row,
  Swipe,
  SwipeItem,
  Lazyload,
  Image,
  Progress,
  Panel,
  Divider,
  Grid,
  GridItem,
  Tabbar,
  TabbarItem,
  NavBar,
  Search,
  SwipeCell,
  List,
  PullRefresh,
  Cell,
  CellGroup,
  Sticky,
  Dialog,
  Toast,
  Field,
  Form,
  Picker,
  Popup,
  DatetimePicker,
  Tab,
  Tabs,
  Switch,
  Checkbox,
  CheckboxGroup,
  RadioGroup,
  Radio,
  Tag,
  NumberKeyboard,
  NoticeBar,
  Uploader,
  Circle,
  ActionSheet,
  CountDown,
  Overlay,
  DropdownMenu,
  DropdownItem,
  Rate,
  AddressList,
  Step,
  Steps,
  Icon,
  ImagePreview
} from 'vant';
Vue.use(Button).use(Col).use(Row).use(Swipe).use(SwipeItem).use(Lazyload).use(Image).use(Progress).use(Panel)
  .use(Divider).use(Grid).use(GridItem).use(Tabbar).use(TabbarItem).use(NavBar).use(Search).use(SwipeCell)
  .use(List).use(PullRefresh).use(Cell).use(CellGroup).use(Sticky).use(Dialog).use(Toast).use(Field).use(Form)
  .use(Picker).use(Popup).use(DatetimePicker).use(Tab).use(Tabs).use(Switch).use(Checkbox).use(CheckboxGroup)
  .use(Radio).use(RadioGroup).use(Tag).use(NumberKeyboard).use(NoticeBar).use(Uploader).use(Circle).use(ActionSheet)
  .use(CountDown).use(Overlay).use(DropdownMenu).use(DropdownItem).use(Rate).use(AddressList).use(Step).use(Steps).use(Icon)
  .use(ImagePreview);
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



